import axios from 'axios';

const uploadBoard = async(title, category, content, uri, image) => {
  const ret = await axios.post('http://3.38.193.180:3030/upload', {
    title: title,
    content: content,
    category: category,
    uri: uri,
    imgURI: image,
  })
  return ret;
};

export {uploadBoard};
