import React, { useEffect, useState } from 'react';
import { Image, ImageBackground, Pressable, StyleSheet, Text, View } from 'react-native';
import { loadAsync } from 'expo-font';

const MainImg = ({ navigation }) => {
  const searchPage = () => {
    navigation.push('Search');
  };
  const [fontsLoaded, setFontsLoaded] = useState(false);
  useEffect(() => {
    const getFonts = async () => {
      await loadAsync({
        'SourceHanSerifK-VF': require('../../assets/fonts/SourceHanSerifK-VF.otf'),
      });
      setFontsLoaded(true);
    }
    if (!fontsLoaded) {
      getFonts();
    }
  }, []);
  return fontsLoaded ? (
    <>
      <ImageBackground
        source={require('../../assets/img/main.png')}
        style={styles.container}>
        <View style={styles.container.topItem}>
          <Text style={styles.container.topItem.logo}>LIFE 100</Text>
          <Pressable onPress={searchPage}>
            <Image
              source={require('../../assets/img/search.png')}
              style={styles.container.topItem.searchButton}
            />
          </Pressable>
        </View>
        <View style={styles.container.container}>
          <View style={styles.container.container.slogan}>
            <Text style={styles.container.container.slogan.title}>
              100세 시대의 불안한 미래,
            </Text>
            <Text style={styles.container.container.slogan.title2}>
              우리에게 꼭 필요한 노후정보
            </Text>
          </View>
        </View>
      </ImageBackground>
    </>
  ) : <></>;
};

export default MainImg;

const styles = StyleSheet.create({
  container: {
    width: '100%',
    height: 390,
    flexDirection: 'column',
    justifyContent: 'space-between',
    // backgroundColor: '#FFFFFF',
    topItem: {
      flexDirection: 'row',
      justifyContent: 'space-between',
      alignContent: 'center',
      width: '80%',
      height: 29,
      marginTop: 43,
      marginLeft: '10%',
      logo: {
        height: 48,
        lineHeight: 48,
        fontSize: 20,
        fontWeight: '800',
        color: '#000000',
        fontFamily: 'SourceHanSerifK-VF',
        textShadowColor: '#FFFFFF',
        textShadowOffset: {
          width: 1,
          height: 1,
        },
      },
      searchButton: {
        width: 48,
        height: 48,
      },
    },
    container: {
      width: '100%',
      height: 82,
      backgroundColor: 'rgba(40,40,40,0.4)',
      slogan: {
        width: 334.14,
        height: 56.8,
        flexDirection: 'column',
        justifyContent: 'space-around',
        marginTop: 13,
        marginLeft: 22,
        title: {
          height: 27.55,
          color: '#FFFFFF',
          fontSize: 20,
          fontWeight: '800',
          fontFamily: 'SUIT-Bold',
        },
        title2: {
          height: 27.55,
          color: '#FFFFFF',
          fontSize: 20,
          fontWeight: '800',
          fontFamily: 'SUIT-Bold',
        },
      },
    },
  },
});
